import React, {useEffect, useState} from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import firebase from 'firebase/app';
//import '@firebase/testing';
import "firebase/auth";
import "firebase/functions";


import {Helmet} from 'react-helmet';
import {log} from 'utilities/Measure.js';
import { globalHistory } from "@reach/router";
import Text from 'views/Text/Text/v1/Text.js';
import GetGapi from './GetGapi';
import LoadScript from './../LoadScript';
import { navigate } from "gatsby";
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import {getUrlHash, getUrl} from 'utilities/Format';
import IconButton from 'views/IconButton/v1/IconButton.js';

import config from './../config';
import './styles.css';


const signInOptions = [
    {
        provider: firebase.auth.TwitterAuthProvider.PROVIDER_ID
    },
    {
        provider: firebase.auth.FacebookAuthProvider.PROVIDER_ID
    }
]

const getGoogleProvider = (permissions) => {
    return {
        provider: firebase.auth.GoogleAuthProvider.PROVIDER_ID,
        scopes: permissions, 
        customParameters: {
            // Forces account selection even when one account
            // is available.
            //access_type: 'offline',
            'include_granted_scopes': true, 
            //prompt: 'select_account'
          }
    }
}

const uiConfig = {
    signInFlow: 'redirect',
    callbacks: {}
}
const URL_HASH = "#signed-in";
const TEMP_HASH = "#signing-in";
//SignInHoc(AssignmentView, id, needsGoogle, needsGapi, permissions);
const SignInHoC = (View, redirectUrl, needsGoogle) => props => {
    if(!props.doSubscribe){
        throw Error(`SignInHoc must be wrapped by subscribable`);
    }
    const [error, setError] = useState(null);
    const [isGapiReady, setIsGapiReady] = useState(false);
     const [user, setUser] = useState(false);
    const [uiInstance, setUiInstance] = useState(null);

    const onAuthStateChanged = (user) => {
        setUser(getUser(user))
    }
    useEffect(()=>{
        return ()=>{
            deleteUi(uiInstance || getUi());
        }
    }, 
    [uiInstance])
    useEffect(()=>{
        if(isSigningIn() && window.firebaseui){
            showSignInUi();
        }
    }, [isGapiReady, user])
    useEffect(()=>{
        /*dont know why this needs to be here, but the ui doesn't show without it*/
        window.firebase = firebase; 
        if(firebase.apps.length === 0){
            
            firebase.initializeApp(config);
            if(globalHistory.location.hostname === 'localhost'){
              //firebase.functions().useFunctionsEmulator("http://localhost:5001");
            }
          }
        if(window && window.gapi && !isGapiReady){
            setIsGapiReady(true);
        }
        firebase.auth().onAuthStateChanged(onAuthStateChanged);
        setFirebaseToWindow();
    }, [])
    const watchConnection = () => {
        var connectedRef = firebase.database().ref(".info/connected");
        connectedRef.on('value', function(connectedSnap) {
            setConnected(connectedSnap.val() === true);
        })
    }
    const setFirebaseToWindow = (ref) => {
        window.firebase = firebase; 
    }

    const onSignOut = () => {
        return props.doSubscribe('signInOut', ()=>{return firebase.auth().signOut()},
        e => {
            //resetUi();
            deleteUi(getUi());
            navigate(getUrl()) 
            return {success: 'logged out'}
        },
        e => {
            return {error: `failed to sign out.`}
        },
        setError);
    }

    const showSignInUi = (permissions, attempts = 0) => {
        console.log(`SignIn  showSignInUi`, props);
        let ui = getUi();
        if(!ui){
            try{
                firebase.auth().setPersistence(firebase.auth.Auth.Persistence.NONE)
                //firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION)
                resetUi(ui)
                uiConfig.signInSuccessUrl = redirectUrl + URL_HASH;
                window.location = getUrl() + TEMP_HASH;
                uiConfig.callbacks.signInSuccessWithAuthResult =  postSignIn;
             //   uiConfig.callbacks.signInFailure = (e) => {log(`bughunt SignInHoc callback signInFailure`, e)};
                startUi(permissions);
            }catch(e){
                if(attempts < 1){
                    showSignInUi(permissions, attempts + 1);
                }
            }
        }else{
            navigate(getUrl() + TEMP_HASH);
        }
    }
    const getUi = ()=>{
       // if(!isMounted) return null;
       let fui;
       if(firebaseui){
        fui = firebaseui.auth.AuthUI.getInstance(getUiId());
       }
       if(!fui){
           fui = uiInstance;
       }
        return fui;
    }
    const deleteUi = (fui) =>{
        setUiInstance(null);
        if(fui) fui.delete();
        
    }
    const resetUi = (fui) => {
        setUiInstance(null);
        if(fui) fui.reset();
    }
    const startUi = (permissions) => {
       
        let newfui = getUi();
        if(!newfui){
            
            let a = firebase.auth();
            newfui = new firebaseui.auth.AuthUI(firebase.auth(), getUiId());
        }
        let google = getGoogleProvider(permissions);
        newfui.start('#' + getUiId(), {...uiConfig, 
            signInOptions: [google, ...needsGoogle?[]:signInOptions]
        });
    }
    const postSignIn = (authResult) => {
        if(window && window.gapi && window.gapi.client && authResult && authResult.credential){
            gapi.client.setToken({access_token: authResult.credential.accessToken});
        }
        return true;
    }


    const isSigningIn = () => {
        return getUrlHash() === TEMP_HASH;
    }
    const cancelSignIn = () => {
        navigate(getUrl())
    }
    const isSignedIn = () => {
     //   if(connected && firebase.apps.length > 0 && user) return true;
        if(user) return true;
        return false;
        //return connected && firebase.apps.length > 0 && firebase.auth().currentUser;// && !explicitSignOut;
    }
    const deleteUser = () => {
        let u = firebase.auth().currentUser;
        if(u){
            return ()=>{
                return u.delete()
            }
        }
    }
    const onGapiIsReady = () => {
        setIsGapiReady(true); //callApiOnGapiReady
    }
    const getUser = (u) => {
        if(u){
           //let u = user;
            return {uid: u.uid, email: u.email, displayName: u.displayName, photoURL: u.photoURL, 
                phoneNumber: u.phoneNumber, createTime: u.metadata.creationTime, lastTime: u.metadata.lastSignInTime,
                provider: u.providerData.reduce((agg, el, i)=>{
                    return agg + `${i > 0?', ':''}${el.providerId}`;
                }, '')}
        }
        return null;
    }

    const getUiId = () => { 
        return `firebaseui-auth-container`
    }
    return (
            <SignInWrapper>
                <Helmet>
                    <link type="text/css" rel="stylesheet" href="https://www.gstatic.com/firebasejs/ui/4.3.0/firebase-ui-auth.css" />
                </Helmet>
                <GetGapi onApiInit={onGapiIsReady} />
                <LoadScript onLoad={setFirebaseToWindow} 
                    script="https://www.gstatic.com/firebasejs/ui/4.3.0/firebase-ui-auth.js"/>

     
                <StateBackground signingIn={isSigningIn()}>
                    <StateView signingIn={isSigningIn()} className="bg-grad-pp">
                        <IconButton 
                            className="sign-in-close"
                            icon="icon-cross"
                            onInput={cancelSignIn}
                            color="primary"
                            bgColor="black"
                        />
                        
                        {isSigningIn() && <div>
                            <Text key="auth-message" id="auth-message" fontSize="remS" className="inline">
                                By signing in, some of your personal information will be used for authentication.  
                            </Text>
                            <a href={'/privacy'} trget="_blank">Read more here</a>
                        </div>}
                
                        
                        {error && <Text key="sign-error" id="sign-error"  className="text-center" >error</Text>}
                        {/* {!isOnline && <Text key="sign-offline" id="sign-offline"  className="text-center" >you are offline</Text>} */}

                        <div id={getUiId()} className="sign-in-wrapper" />
                    </StateView>
                </StateBackground>
                {isSignedIn() && <SignOutWrapper>
                    <Text key="sign-out" id="sign-out" className="text-underline" onInput={onSignOut} >sign out</Text>
                </SignOutWrapper>}
               {isGapiReady &&  <div className={`actionView ${isSignedIn()?'signedIn':'notSignedIn'}`}>
               <View {...props} isSignedIn={isSignedIn()} user={user} doSignOut={onSignOut} deleteUser={deleteUser}
                isGapiReady={isGapiReady} doSignIn={showSignInUi} 
                />
                </div>}
            
            </SignInWrapper>
    )
}


export default SignInHoC;

const SignOutWrapper = styled.div`
    position: fixed;
    top:0px;
    right:0px;
    color: orange;
`
// height: 100%;
const SignInWrapper = styled.div`
    text-align: center !important;

`
const StateBackground = styled.div`
    position: absolute;
    top:0px;
    bottom:0px;
    left: 0px;
    right:0px;
    background-color: rgba(0,0,0,0.8);
    transform: translateY(${props => props.signingIn?'0%':'-100%'});
    transition: transform 1s ease-out;
    z-index: ${props => props.theme.zUi};
`
const StateView = styled.div`

    width: 95%;
    margin: auto;
    margin-top:100px;
    z-index: ${props => props.theme.zTop};
    position: relative;
    .sign-in-close{
        position: absolute;
        top: -25px;
        left: 0px;
    }
`