import React, {useState, useEffect} from 'react';
import Button from 'views/Button/v1/Button';
import Text from 'views/Text/Text/v1/Text';
import Loading from 'views/Loading/v1/Loading';
import SelectCourse from 'apis/classroom/getters/SelectCourse/index';
import SelectCourseWork from 'apis/classroom/getters/SelectCourseWork/index';
import {teacherMarkPermissions} from 'apis/config';
import SubscriptionHoC from 'views/hoc/Subscribable';
import Input from 'views/Input/v1/Input';
import {lookAtAssignments, markGradedAssignments, markAssignments } from 'apis/firebase/gapiFunctions';
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import {log} from 'utilities/Measure';
import {AnimatePresence, motion} from 'framer-motion'; 
import {ANIM_LAYOUT_OVERLAP} from 'game/tree/Animations.js';
//will need grade to assign
const ST_DEFAULT = 'def', ST_COURSE = 'course', ST_COURSE_WORK = 'coursework', ST_PROCESSING ='processing', 
ST_SUBMISSIONS='submissions', ST_GETTING_ASSIGNMENTS = 'gettingAssignments', ST_NOT_FOUND = 'notfound', 
ST_VIEWING="viewing", ST_MARKING="marking", ST_SUCCESS = 'success', ST_ERR = 'err';
const MAX_POINTS = 100; //TODO, all assignments should reference the same value
const MarkAssignmentsView = (props) => {
    const [permissions] = useState(teacherMarkPermissions);
    const [error, setError] = useState(null);
    const [fsm, setFsm] = useState(ST_COURSE);
    const [courseId, setCourseId] = useState(null);
    const [courseWorkId, setCourseWorkId] = useState(null);
    const [assignments, setAssignments] = useState(null);
    const [grades, setGrades] = useState({});
    const [totalAssignments, setTotalAssignments] = useState(0);

    const changeState = (key, payload) => {
        if(fsm === ST_COURSE){
            setCourseId(payload.courseId);
            setFsm(ST_COURSE_WORK);
        }else if(fsm === ST_COURSE_WORK){
            setCourseWorkId(payload.courseWorkId);
           
            getAssignments(courseId, payload.courseWorkId);
            // setFsm(ST_MARKING);
            // justMarkAssignments(courseId, payload.courseWorkId);
        }else if(fsm === ST_VIEWING){
            setFsm(ST_MARKING);
        }
        else if(fsm === ST_DEFAULT){
            setFsm(ST_COURSE)
        }
    }

    const getAssignments = (cId, cwId) => {
        //get all submissions, get their attachments, patch each grade in
        setFsm(ST_COURSE_WORK);
        props.doSubscribe('markAssignmentGetAssignments', ()=>{return lookAtAssignments(cId, cwId)},
        (e) => {
            if(e && e.results && e.results.length > 0){
                let nGrades = {...grades};
                e.results.map(g => {
                    // if(g.gameGrade || g.gameGrade === 0){
                    //     nGrades[g.id] = g.gameGrade;
                    // }else{
                    //     nGrades[g.id] = 50;
                    // }
                    nGrades[g.id] = g.gameGrade;
              })
              setAssignments(e.results);
               setTotalAssignments(e.totalAssignments)
               setGrades(nGrades);
               setFsm(ST_VIEWING);
             
            }else{
               setFsm(ST_NOT_FOUND)
            }
        }, 
        cloudCatch,
        setError)
     }
    const doMarkAssignments = (returnAssignment) => {
       setFsm(ST_MARKING); //import {firestoreCatch} from 'apis/firebase/utilities';
       props.doSubscribe('markAssignments', 
       ()=>{return markGradedAssignments(assignments, grades, returnAssignment)},
       (e) => {
            setFsm(ST_SUCCESS);
        }, cloudCatch, setError);
    }
    const handleGradeChange = (id, value) => {
        setGrades({...grades, [id]: value});
    }
    const getAssignmentsLink = () => {
        if(assignments){
            let link = assignments[0].link;
            let url = link.split('/').slice(0, -1).join('/') + "/all";
            return <Text className="text-center" color="secondary">
                        <a href={url} target="_blank">View assignments</a>
                    </Text>
        }
    }
    const renderState = () => {
        switch(fsm){
            // case ST_GETTING_ASSIGNMENTS: return <Loading text="Retrieving Assignments"/>
            case ST_NOT_FOUND: return <div>
                <Text className="text-center" color="primary">Resource not found</Text>
                <Text className="text-center">If there are no turned in assignments, there are no assignments to grade. </Text>
            </div>
            case ST_SUCCESS: return <div>
                <Text className="text-center" color="primary">Assignments have been updated</Text>
                {getAssignmentsLink()}
            </div>
            case ST_MARKING: return <Loading text="Marking Assignments"/>
            case ST_VIEWING: 
                return <div>
                    <Text className="text-center" tag="p" color="primary">
                        {assignments.length}/{totalAssignments} Assignments turned in
                    </Text>
                    <Text className="text-center">Update any grade by clicking the checkmark</Text>
                    <div>
                        {assignments && assignments.map((e, i) => {
                            return <div key={i}>
                                <Text>
                                    <a href={e.link} target="_blank">view assignment</a>
                                </Text>
                                <Text>
                                    Suggested Grade: {e.gameGrade}
                                </Text>
                                <Text>
                                    Your Grade:
                                </Text>
                                <Input key={e.id} id={e.id} value={grades.hasOwnProperty(e.id)?grades[e.id]:e.gameGrade} 
                                    onInput={handleGradeChange} placeholder="50" inputWidth="50px" autoUpdate
                                    validate={{numericality: {greaterThanOrEqualTo: 0, lessThanOrEqualTo: 100, onlyInteger: true}}} />
                            </div>
                        })}
                    </div>
                    <Button onInput={()=>{doMarkAssignments(true)}} compact glow>Grade and return assignments</Button>
                    <Button onInput={()=>{doMarkAssignments(false)}} compact glow>Draft grade assignments</Button>
                </div>
            case ST_COURSE_WORK: return <SelectCourseWork id={ST_COURSE_WORK} courseId={courseId} onSelect={changeState} 
                    permissions={permissions} onError={setError} />
            case ST_COURSE: 
            default: return <SelectCourse id={ST_COURSE} onSelect={changeState} 
                     permissions={permissions} onError={setError}/>
        }
    }
    const wrapView = (view) => {
        return <motion.div key={fsm} {...ANIM_LAYOUT_OVERLAP}>{view}</motion.div>
    }
    return <AnimatePresence>
            {error && <motion.div  key="error" {...ANIM_LAYOUT_OVERLAP}>
                <Text className="text-center" color="secondary">Error: {error}</Text>
            </motion.div>}
            {!error && wrapView(renderState())}
      </AnimatePresence>
}

export default SubscriptionHoC(MarkAssignmentsView);