import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Select from 'views/Select/v2/Select';
import Text from 'views/Text/Text/v1/Text';
import Button from 'views/Button/v1/Button';
import Loading from 'views/Loading/v1/Loading';
import SubscriptionHoC from 'views/hoc/Subscribable';
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import {listStudents} from 'apis/firebase/gapiFunctions';
import {log} from 'utilities/Measure.js';
import {AnimatePresence, motion} from 'framer-motion'; 
import {ANIM_LAYOUT_OVERLAP} from 'game/tree/Animations.js';

const SelectStudents = (props) => {
    const [students, setStudents] = useState(null);
    const [selectedStudents, setSelectedStudents] = useState(null);
    useEffect(()=>{
        getStudents();
    }, [])
    const getStudents = () => {
        if(props.courseId){
            props.doSubscribe('selectStudents', 
            ()=>{return listStudents(props.courseId)},
            e => {
                setStudents(e.students.map(e => {
                    return {label: e.profile.name.fullName, value: e.userId};
                }));
            },
            cloudCatch, props.onError)
        }else{
            throw new Error(`insufficient information to retrieve students list`);
        }
    }
    const handleSelect = (id, val) => {
        setSelectedStudents(val);
    }
    const onAccept = () => {
        props.onInput(props.id, selectedStudents.map(e => {return {name: e.name, id: e.value}}));
    }
    const onCancel = () => {props.onInput(props.id, null)}

    return <AnimatePresence>
        {students && <motion.div key="students" {...ANIM_LAYOUT_OVERLAP}>
            <Text text="Select the students you would like to assign this coursework to. You can change this later, too." />
            <Select id="selectStudents" options={students} isMulti placeholder="Select a Students" onInput={handleSelect} />
            <Button onInput={onCancel} bgColor="neutral" width="46%" margin="2%">Cancel</Button>
           {selectedStudents && selectedStudents.length > 0 &&  <Button onInput={onAccept} width="46%" margin="2%" >Okay</Button>}
        </motion.div>}
        {!students && <motion.div key="loading" {...ANIM_LAYOUT_OVERLAP}>
            <Loading delay={0} text="Retrieving your students"/>
            </motion.div>}
    </AnimatePresence>
}

SelectStudents.propTypes = {
    courseId: PropTypes.string,
    onInput: PropTypes.func,
    onError: PropTypes.func,
}

export default SubscriptionHoC(SelectStudents);
