import React, {useEffect, useState} from 'react';
import ErrorBoundary from 'views/Boundary/Boundary.js';
import SiteContainer from 'views/Container/Container.js';

import { navigate, Link } from "gatsby";
import {log, sendEvent} from 'utilities/Measure';
import {Event} from 'utilities/Create';
import Text from 'views/Text/Text/v1/Text';
import Classroom from 'apis/classroom/interfaces/teachers/index';
import Header from 'views/Header/Header.js';
import CourseWorkView from 'views/CourseWorkView/index.js';


const Teachers = (props) => {
  useEffect(()=>{
    props.addMenuItem({
      'title':props.meta.title,
      onBack,
      menuItems: null
    });
  }, []);
    const onBack = () => {
        navigate(props.meta.path);
    }
    return <SiteContainer key={`teacher-${props.meta.import}`} id={`teacher-${props.meta.import}`}>
        <Header center title={`Teachers Page`} />
        <CourseWorkView meta={props.meta} />
        <Classroom meta={props.meta} />
    </SiteContainer>
}


const TeachersWrapper = (props) => {
    return <Teachers 
        meta={props.data.game}
        site={props.data.site.siteMetadata}
        addMenuItem={props.addMenuItem}
    />
}
export default TeachersWrapper;

export const pageQuery = graphql`
  query TeacherSectionByID($import: String!) {
    site {
      siteMetadata {
        ...siteDataFragment
      }
    },
    game: gamesMetaJson(import: { eq: $import }) {
      id
      path
      import
      complete
      title
      topics
      snippet
    }
  }
`
  