import React, {useState, useEffect} from 'react';
import LoadScript from './../LoadScript'
import Subscribable from 'views/hoc/Subscribable';
import {log, sendError} from 'utilities/Measure.js';


let clientId = "392303660392-if4osc2chaurcd8a5o6btcuud6r3rj91.apps.googleusercontent.com";

const config = {
    clientId: "392303660392-if4osc2chaurcd8a5o6btcuud6r3rj91.apps.googleusercontent.com",
    scope: 'https://www.googleapis.com/auth/drive.file',
    discoveryDocs: [
      "https://www.googleapis.com/discovery/v1/apis/drive/v3/rest",
      "https://www.googleapis.com/discovery/v1/apis/classroom/v1/rest",
    ]
};

const Gapi = (props) => {

    const onLoad = (e) => {

        gapi.load('client', () => {
            props.doSubscribe('getGapi', ()=>{return gapi.client.init(config)},
            (r)=>{
              props.onApiInit('gapi');
            },
            e => {
              sendError(`gapi client init rejected`);
            },
            (e)=>{
              sendError(`gapi client onError called`)
            })
        });
    }

    return <LoadScript apiKey="cli" script='https://apis.google.com/js/api.js' 
        onLoad={onLoad} />
}

export default Subscribable(Gapi);