import React, {useState, useEffect} from 'react';
//import SignIn from './../../SignIn/SignIn';
// import Text from 'game/Views/TextManager/TextManager.js';
import TextStr from 'views/Text/Text/v1/Text';
import {Link} from 'gatsby';
import Btn from 'views/Button/v1/Button.js';
import Loading from 'views/Loading/v1/Loading';
import SelectCourse from 'apis/classroom/getters/SelectCourse/index';
import SelectStudents from 'apis/classroom/getters/SelectStudents/index';
import {getCourses, createCoursework, modifyAssignees} from 'apis/firebase/gapiFunctions';
import { globalHistory } from "@reach/router";
import SubscriptionHoC from 'views/hoc/Subscribable';
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import Time from 'views/Time/index';
import {log} from 'utilities/Measure';
import {teacherCreatePermissions} from 'apis/config';
import {getUrl, getHost, getFullUrl, getParentUrl} from 'utilities/Format.js';
import {AnimatePresence, motion} from 'framer-motion'; 
import {ANIM_LAYOUT_OVERLAP} from 'game/tree/Animations.js';

const COURSES = 'courses', FSM_CREATE = 'create', FSM_COURSE = 'course', FSM_CREATING = 'creating', 
    FSM_EDITING_CW = 'editing', FSM_DUE = 'due', FSM_STUDENT = 'student', FSM_COMPLETE = 'complete', FSM_START ='start';
const CreateCourseWork = (props) => {
    log(`CreateCourseWork props`, props);
    const [error, setError] = useState(null);
    const [fsm, setFsm] = useState(FSM_CREATE);
    const [selectStudents, setSelectStudents] = useState(false);
    const [courseWorkId, setCourseWorkId] = useState(null);
    const [courseId, setCourseId] = useState(null);
    const [courseWorkUrl, setCourseWorkUrl] = useState(null);
    const [date, setDate] = useState(null);

    const onError = (err) => {
        setError(err)
    }
    const getAmericanHours = (hour, date) => {
        let r = 0;
       let diff = 10;
        if(hour < 10){
            r = 24-(10-hour);
        }else if(hour > 10){
            r =  hour - 10;
        }
        let f =  r%24;
        return f;
    }
    const getDay = (hours, date) => {
        let v = date;
        if(hours < 10) v = v - 1;
        return v;
    }
    const createCourseWorkForCourse = (key, payload) => {
   
        let id = payload.courseId;
        setCourseId(id);
        setFsm(FSM_CREATING);
        //let url = globalHistory.location.origin + globalHistory.location.pathname;

        let url = getParentUrl(getFullUrl(), 1);
  
        //due date?
        let cw = {
            courseId: id, 
            'title': `Assignment: ${props.meta.title}`,
            'description': `complete the game, then click and complete 'submit assignment' in the report section: ${props.meta.snippet}`,
            'materials': [
               {'link': { 'url': url }}
            ],
            'workType': 'ASSIGNMENT',
            'state': 'PUBLISHED',
            submissionModificationMode: 'MODIFIABLE_UNTIL_TURNED_IN',

            maxPoints: 100,
            associatedWithDeveloper: true
        }
        if(payload.date){
            cw.dueDate = {
                year: payload.date.year,
                month:payload.date.month + 1,
                day: getDay(payload.date.hours, payload.date.date)
            };
            cw.dueTime = {
                hours: getAmericanHours(payload.date.hours, payload.date),
                minutes: payload.date.minutes,
                seconds:0,
                nanos: 0
            };
        }
        if(payload.student){
            cw.assigneeMode = "INDIVIDUAL_STUDENTS";
            cw.individualStudentsOptions = payload.students;
        }
        props.doSubscribe('createCourseWork', 
            ()=>{return createCoursework(cw)},
            e => {
                log(`CreateCourseWork coursework result`, e);
                setCourseWorkId(e.id);
                setCourseWorkUrl(e.alternateLink);
                setFsm(FSM_COMPLETE);
            },
            cloudCatch,
            onError
        )
    }
    const changeFsm = (id, val) => {
        if(id === 'mod-yes'){
            setSelectStudents(true);
            setFsm(FSM_COURSE);
        }else if(id === 'mod-no'){
            setSelectStudents(false);
            setFsm(FSM_COURSE);
        }else if(id === COURSES){
            setCourseId(val.courseId);
            setFsm(FSM_DUE);
        }else if(id === FSM_DUE){
            //set date
            let d = {
                year: val.getFullYear(),
                month: val.getMonth(),
                date: val.getDate(),
                hours: val.getHours(),
                minutes: val.getMinutes()
            }
            setDate(d);
            if(selectStudents){
                setFsm(FSM_STUDENT);
            }else{
                createCourseWorkForCourse(id, {courseId, date: d}) 
            }
        }else if(id === FSM_STUDENT){
            //if val is null, teacher cancelled, revert state
            if(val){
                createCourseWorkForCourse(id, {students: val.map(e => e.id), courseId, date}) 
            }else{
                setFsm(FSM_CREATE);
            }
        }else if(id === FSM_START){
            setFsm(FSM_CREATE);
        }
        // else if(id === FSM_COURSE){
        //     setFsm(FSM_STUDENT);
        // }
    }

    const renderSelectCourse = () => {
        if(fsm === FSM_CREATE){
            return <div key={FSM_CREATE}>
                {/* <Text text={[{text: 'Would you like to assign the course work to individual students, instead of the whole class?'}]} /> */}
                <TextStr className="block text-center">Would you like to assign the course work to the whole class or individual students?</TextStr>
                <Btn id="mod-no" onInput={changeFsm} compact>the whole class</Btn>
                <Btn id="mod-yes" onInput={changeFsm} compact>individual students</Btn>
               
            </div>
            
        }else if(fsm === FSM_COURSE){
            return <SelectCourse key={FSM_COURSE} id={COURSES} onSelect={changeFsm} onError={onError} />
        }else if(fsm === FSM_CREATING){
            // return <div><Text text="creating course work" /></div>
            return <Loading key={FSM_CREATING} delay={0}  text="Creating course work"/>
        }else if(fsm === FSM_STUDENT){
            return <SelectStudents key={FSM_STUDENT} id={FSM_STUDENT} onInput={changeFsm} onError={onError} courseId={courseId}  />
        }else if(fsm === FSM_EDITING_CW){
           // return <div><Text text="updating course work with selected student ids" /></div>
            return <Loading key={FSM_EDITING_CW} delay={0} text="Updating course work with selected student ids"/>
        }else if(fsm === FSM_COMPLETE){
            return <div key={FSM_COMPLETE} className="text-center">
                <TextStr className="block text-center">Course work created and assigned</TextStr>
                <a href={courseWorkUrl} target="_blank"><TextStr>View Assignment</TextStr></a>
            </div>
        }else if(fsm === FSM_DUE){
            return <div key={FSM_DUE}>
                <TextStr>Select due date</TextStr>
                <Time id={FSM_DUE} onInput={changeFsm} />
            </div>
        }else if(fsm === FSM_START){
            return <div key={FSM_START}>
                        <Btn id={FSM_START} onClick={changeFsm}>Create Assignment</Btn>
                </div>
        }
    }
    const wrapView = (view) => {
        return <motion.div key={fsm} {...ANIM_LAYOUT_OVERLAP}>{view}</motion.div>
    }
    return <AnimatePresence>
            {error && <motion.div key="error" {...ANIM_LAYOUT_OVERLAP}>
                <TextStr  className="text-center" color="error">Error: {error}</TextStr>
            </motion.div>}
            {!error && wrapView(renderSelectCourse())}
      </AnimatePresence>
}

export default SubscriptionHoC(CreateCourseWork);
