const firebase = require("firebase");
// Required for side-effects
require("firebase/functions");
import {log, sendError} from 'utilities/Measure';


export const getPermission = (permissions, onPermitted) => {
    let user = firebase.auth().currentUser;
    let provider = getProvider(firebase.auth().currentUser, permissions);
    return {
        //promise: ()=>{return firebase.auth().signInWithPopup(provider)},
        promise: ()=>{return firebase.auth().signInWithRedirect(provider)},
        then:function(result) {
            if(window && window.gapi){
                gapi.client.setToken({access_token: result.credential.accessToken});
                onPermitted();
            }
          },
        cat:function(error) {
           if(error.message){
            return error.message;
           }
           return error;
        } 
    }
}


const getGoogleProvider = (permissions) => {
    let google =  new firebase.auth.GoogleAuthProvider();
        if(permissions){
            permissions.map(e => {
                google.addScope(e);
            })
        }
        return google;
}
const getProvider = (user, permissions) => {
    let providerId = user?user.providerData[0].providerId:null;
    if(permissions){
        return getGoogleProvider(permissions);
    }else{
        switch(providerId){
            case 'google.com': return getGoogleProvider(permissions);
            case 'twitter.com': return new firebase.auth.TwitterAuthProvider();
            case 'facebook.com': return new firebase.auth.FacebookAuthProvider();
        }
    }

    return null;
}

export const cloudThen = (callback) => (e) => {
    if(e.data && !e.data.hasOwnProperty('fail')){
        callback(e.data);
    }else if(e.data.hasOwnProperty('fail')){
        if(typeof e.data.fail === 'string'){
            throw new Error(e.data.fail);
        }else{
            throw new Error('cloud function failed');
        }
        
    }else{
        throw new Error('No such record found');
    }
}
export const cloudThenInline = (e) => {
    if(e.data && !e.data.hasOwnProperty('fail')){
        return e.data;
    }else if(e.data.hasOwnProperty('fail')){
        if(typeof e.data.fail === 'string'){
            throw new Error(e.data.fail);
        }else{
            throw new Error('cloud function failed');
        }
        
    }else{
        throw new Error('No such record found');
    }
}
export const cloudCatch = (e) => {
    if(e.result && e.result.error){
        sendError(`cloudCatch ${e.result.error.message}`, false);
        return e.result.error.message;
    }else if(e.error){
        sendError(`cloudCatch ${e.error}`, false);
        return e.error;
    }else if(e.message){
        sendError(`cloudCatch ${e.message}`, false);
        return  e.message;
    }else if(typeof e === 'string'){
        sendError(`cloudCatch ${e}`, false);
        //may be an error string passed from thrown then error
        return e;
    }else{
        sendError(`cloudCatch Incorrectly formatted cloudCatch error`, false);
        //throw new Error('Incorrectly formatted cloudCatch error', e);
        return 'Incorrectly formatted cloudCatch error';
    }
}


