import React, {useEffect, useState} from 'react';
import MarkAssignments from 'apis/classroom/MarkAssignments/index';
import {teacherMarkPermissions} from 'apis/config';
import Button from 'views/Button/v1/Button.js';
import Modal from 'views/Modal/v1/Modal';
import { navigate } from "gatsby";
import Text from 'views/Text/Text/v1/Text';
import {log} from 'utilities/Measure';
import Box from 'views/Box/v1/Box';

export const STORAGE_VALUE = 'mark-assignment';
const styleTitle = {position: 'absolute', top:0, left: '50%', transform: 'translateX(-50%)'};
const MarkAssignmentsView = (props) => {
 
    if(props.sendTo === STORAGE_VALUE){
        return <React.Fragment>
                <div style={styleTitle}>
                <Text text="Mark Assignments" tag="h2" className="text-center"/>
                </div>
                
                <MarkAssignments {...props} meta={props.meta} />
            </React.Fragment>
    }else{
        return null;
    }
}

export default MarkAssignmentsView ;

export const MarkAssignmentsTrigger = (props) => {
    return <Box>
        <Button onInput={()=>{
            props.activate(teacherMarkPermissions, STORAGE_VALUE);
        }}>Mark Assignments</Button>
    </Box>
}