import React, {useEffect, useState} from 'react';
import styled from 'styled-components';
import Text from 'views/Text/Text/v1/Text';
import Subscribable from 'views/hoc/Subscribable/index';
import {log} from 'utilities/Measure';
import {getUrl} from 'utilities/Format';
import SignIn from 'apis/SignIn/SignIn';
import Modal from 'views/Modal/v1/Modal';
import {storeItem, getItem} from 'utilities/Storage.js';
import CreateAssignment, {CreateAssignmentTrigger, STORAGE_VALUE as ID_CREATE} from './CreateAssignment/index';
import MarkAssignments, {MarkAssignmentsTrigger, STORAGE_VALUE as ID_MARK} from './MarkAssignments/index';
import {motion} from 'framer-motion';
import {SLIDE_UP} from 'game/tree/Animations.js';

const STORAGE_ID = 'teacher-action';
const ClassroomTeachers = (props) => {
    const [sendTo, setSendTo] = useState(getItem(STORAGE_ID));
    const [active, setActive] = useState(sendTo?true:false);
    const [error, setError] = useState(null);

    const deactivate = () => {
        setActive(false);
        setSendTo(null);
        storeItem(STORAGE_ID, null);
        props.doSignOut(); 
    }
    const activate = (permissions, target) => {
        storeItem(STORAGE_ID, target);
        props.doSignIn(permissions); 
        setActive(target?true:false);
    }
    
    return <TeacherWrapper {...SLIDE_UP}>
        {error && <Text color="red" className="block text-center">{error}</Text>}
        <CreateAssignmentTrigger {...props} id={ID_CREATE} activate={activate} />
        {/* {isDevelopment() && <MarkAssignmentsTrigger {...props} id={ID_MARK} activate={activate} />} */}
        <MarkAssignmentsTrigger {...props} id={ID_MARK} activate={activate} />
       <div className="teacher-actions">
      
            <Text text="How to Create Assignments" tag="h4" />
            <ol className="teacher-list">
                <li><Text>Click 'Create Coursework'</Text></li>
                <li><Text>Select Class</Text></li>
                <li><Text>Select Students to assign the coursework to</Text></li>
                <li><Text>Select a due date</Text></li>
                <li><Text>Wait for student to complete game and submit their 
                    Google Doc automated game performance reports</Text></li>
                <li><Text>Grade assignments from Google Classroom portal</Text></li>
            </ol>
       </div>
       <Modal active={sendTo && props.isSignedIn} onClose={deactivate} className="bg-grad-pp">
            <CreateAssignment {...props} sendTo={sendTo} />
            {/* {isDevelopment() && <MarkAssignments {...props} sendTo={sendTo} />} */}
            <MarkAssignments {...props} sendTo={sendTo} />
        </Modal>
    </TeacherWrapper>
}

const AuthenticatedUserAccount = Subscribable(
    SignIn(ClassroomTeachers, getUrl(), true)
);

export default AuthenticatedUserAccount;

const TeacherWrapper = styled.div`
    animation: slideUp 1s ease;
    text-align: center;
    .teacher-actions{
        max-width: ${props => props.theme.mediaM};
        margin:30px auto;
    }
    .teacher-list li{
        list-style-position: inside;
        color: ${props => props.theme.secondary};
        text-align:left;
    }
`
