import React, {useEffect, useState} from 'react';
import Box from 'views/Box/v1/Box';
import CreateAssignment from 'apis/classroom/CreateCourseWork/index';
import {teacherCreatePermissions} from 'apis/config';
import Button from 'views/Button/v1/Button.js';
import Text from 'views/Text/Text/v1/Text';
import {log} from 'utilities/Measure';

export const STORAGE_VALUE = 'create-assignment';
const styleTitle = {position: 'absolute', top:0, left: '50%', transform: 'translateX(-50%)'};
const CreateView = (props) => {
    if(props.sendTo === STORAGE_VALUE){
        return <React.Fragment>
                <div style={styleTitle}>
                    <Text tag="h2" className="block text-center">Create Coursework</Text>
                </div>
                <CreateAssignment {...props} meta={props.meta} />
            </React.Fragment>
    }else{
        return null;
    }
    
}
// validate={{numericality: {greaterThan: 8}}}
export default CreateView ;

export const CreateAssignmentTrigger = (props) => {
    return <Box>
        <Button onInput={()=>{
            props.activate(teacherCreatePermissions, STORAGE_VALUE);
        }}>Create Coursework</Button>
    </Box>
}