import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {getCourseWork} from 'apis/firebase/gapiFunctions';
import SubscriptionHoC from 'views/hoc/Subscribable';
import Text from 'views/Text/Text/v1/Text';
import Loading from 'views/Loading/v1/Loading';
import IconButton from 'views/IconButton/v1/IconButton';
 import {List, ListItem} from './classroom.css.js';
 import {cloudCatch} from 'apis/firebase/cloudFunctions';
 import Select from 'views/Select/v2/Select';
 import {log} from 'utilities/Measure.js';
 import {AnimatePresence, motion} from 'framer-motion'; 
import {ANIM_LAYOUT_OVERLAP} from 'game/tree/Animations.js';

const PAGE_SIZE = 10;
const SelectCourseWork = (props) => {
    const [courseWork, setCourseWork] = useState(null);
    const [nextPageToken, setNextPageToken] = useState(null);
    useEffect(()=>{
        handleGetCourseWork()
    }, [])
    const handleGetCourseWork =  () => {
        props.doSubscribe('selectCourseWorkGetCoursework', 
            ()=>{return getCourseWork(props.courseId, props.pageSize || PAGE_SIZE, nextPageToken)},
            result => {
                if(result.nextPageToken || (nextPageToken && !result.nextPageToken)){ //if there are > 10 responses, will get token back
                    setNextPageToken(result.nextPageToken); //if token exists, or if there was a token and now is undefined ie end of list
                }
                
                if(result.courseWork){
                    let date;
                    let filteredCw = result.courseWork.filter(f => f.associatedWithDeveloper);
                    if(props.filterByTitle){
                        filteredCw = filteredCw.filter(f => {
                            return f.title.indexOf(props.filterByTitle)  > -1;
                        });
                    }
               
                  
                    setCourseWork(filteredCw.map(cw => {
                       // let hrs = cw.dueTime.hours+10;
                        let adj = (cw.dueTime.hours > 0 && cw.dueTime.hours < 14)?0:1;
                        date = new Date(cw.creationTime); //date.toLocaleDateString()
                        return {label: `Title: ${cw.title}, Due date: ${
                            new Date(
                                cw.dueDate.year,
                                cw.dueDate.month-1,
                                adj + cw.dueDate.day,
                                cw.dueTime.hours
                            ).toLocaleDateString()}`, value: cw.id}
                    }));
                }else{
                    throw new Error(`no coursework found for course ${props.courseId}`);
                }
            },
            cloudCatch,
            props.onError
        );
    }
    const handleSelect = (id, val) => {
        props.onSelect(props.id, {courseId: props.courseId, courseWorkId: val});
    }

    return <AnimatePresence>
        {courseWork && <motion.div key="courswork" {...ANIM_LAYOUT_OVERLAP}>
                <List>
                <div style={{height: '50px'}}>
                {nextPageToken && <IconButton className="nextPage" icon="icon-circle-down" fontSize="remL" 
                    rotate="-90" onInput={handleGetCourseWork} text="Next Page"  />}
                </div>
                <Select id="selectcourseWork" options={courseWork} className="block" placeholder="Select coursework" onInput={handleSelect} />
            </List></motion.div>}
        {!courseWork && <motion.div key="loading" {...ANIM_LAYOUT_OVERLAP}>
            <Loading key="loading"  delay={0} text="Retrieving Courses"/>
        </motion.div>}
    </AnimatePresence>
}

SelectCourseWork.propTypes = {
    id: PropTypes.string,
    course: PropTypes.string,
    onSelect: PropTypes.func
}

export default SubscriptionHoC(SelectCourseWork);