import styled from 'styled-components';

export const List = styled.section`
    padding-left: 0px;
    margin-left: 0px;
    text-align: left;
    position: relative;
    .nextPage{
        position: absolute; 
        top:0px;
        right:0px;
   
    }
    ul{
        margin: 0px auto 30px auto;
    }
`

export const ListItem = styled.div`
   border-bottom: 2px solid white;
   position:relative;

   .item-text{
       display: inline-block;
       width: 70%;
       position: relative;
       top: 10px;
   }
`