import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {getCourses} from 'apis/firebase/gapiFunctions';
import Select from 'views/Select/v2/Select';
import Loading from 'views/Loading/v1/Loading';
import SubscriptionHoC from 'views/hoc/Subscribable';
import {cloudCatch} from 'apis/firebase/cloudFunctions';
import {log} from 'utilities/Measure.js';
import {AnimatePresence, motion} from 'framer-motion'; 
import {ANIM_LAYOUT_OVERLAP} from 'game/tree/Animations.js';

const SelectCourseWork = (props) => {
    const [courses, setCourses] = useState(null);

   useEffect(()=>{
    createCourseWork();
   }, [])
    const createCourseWork = async () => {
            props.doSubscribe('selectCourseWork', getCourses, (e) => {
                if(e.courses && e.courses.map){
                    let options = e.courses.map(course => {
                        return {label: course.name, value: course.id}
                    })
                    setCourses(options);
                }else{
                    props.onError('No courses found for user');
                }
            }, cloudCatch, props.onError)
    }
    const handleSelect = (id, val) => {
        props.onSelect(props.id, {courseId: val});
    }
    return <AnimatePresence>
        {courses && <motion.div key="selectCourse" {...ANIM_LAYOUT_OVERLAP}>
                <Select id="selectCourse" options={courses} className="block" 
                placeholder="Select a Course" onInput={handleSelect} />
            </motion.div>}
        {!courses && <motion.div key="loading" {...ANIM_LAYOUT_OVERLAP}>
                <Loading delay={0} text="Retrieving Courses"/>
            </motion.div>}
    </AnimatePresence>
}

SelectCourseWork.propTypes = {
    id: PropTypes.string,
    onSelect: PropTypes.func
}


export default SubscriptionHoC(SelectCourseWork)
